import React, {useEffect, useState} from 'react';
import RedButton from "./Buttons/RedButton";
import {useInView} from "react-intersection-observer";
import {LazyLoadImage} from "react-lazy-load-image-component";
const FullScreenBgImage = (props) => {



        const scrollEl = document.querySelector("#main-container");
        const { ref: sectionAnimation, inView: mySectionHasAnimations } = useInView({ triggerOnce: true, threshold: 0.02});

        const [button, showButton] = useState(false);

        useEffect(() => {
            props.button === false ? showButton(false): showButton(true);
        },)

        return (
            <div id={props.idSection}>
                <div ref={sectionAnimation} className={'lg:duration-[2000ms]  grid grid-cols-2 lg:m-8 m-5 overflow-hidden'}>
                    <div className={' flex flex-col justify-center items-start w-3/4 mx-auto'}>
                        <div className={'px-4 py-1 rounded-[30px] bg-black text-white mb-8 text-[0.9em]'}>{props.tag}</div>
                        <h2 style={{ whiteSpace: "pre-line" }} className={'mb-6'}>{props.title}</h2>
                        <p className={'mb-8'}>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nunc sed elementum dolor. Curabitur cursus lacus id porta gravida. Suspendisse eget commodo est.</p>
                        <div className={button?'flex items-center space-x-4':'hidden'}>
                            <RedButton text={"Télécharger l'App"} link={props.link}/>
                        </div>
                    </div>
                    <div className={'bg-black/5 lg:rounded-[60px] rounded-[30px] overflow-hidden max-h-[93vh] '}>
                        <LazyLoadImage alt={''} className={'self-end w-[89%] mx-auto'} src={props.src}/>
                    </div>

                </div>
            </div>

        );
    }

export default FullScreenBgImage;
